import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AddS2ApplicationComponent } from './add-s2-application.component';

const routes: Routes = [
  {
    path: '',
    component: AddS2ApplicationComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AddS2ApplicationRoutingModule { }
